import axios from 'axios'
import store from '../store'
import router from '../router'

const instance = axios.create({
  baseURL: process.env.VUE_APP_API_URL,
  withCredentials: true,
  headers: {
    'Content-Type': 'application/json'
  }
})

let timerId

instance.interceptors.request.use(

  function (config) {
    timerId = setTimeout(() => store.commit('showLoader', true), 1000)
    config.headers.auth = `Bearer ${localStorage.getItem('token')}`
    return config; 
  }, 

  function (error) {
    clearTimeout(timerId)
    store.commit('showLoader', false)
    return Promise.reject(error)
  }
  
)

instance.interceptors.response.use(

  (response) => {

    clearTimeout(timerId)
    store.commit('showLoader', false)
    if (response.status == 202) store.commit('showMessage', { 
      color: '#4da64d', 
      icon: 'done', 
      title: 'Успішно', 
      text: response?.data?.message
    })

    return response;

  },

  async (error) => {

    clearTimeout(timerId)
    store.commit('showLoader', false)

    if (error.response.status == 403) router.push('confirm')
    else if (error.response.status == 401 && error.config && !error.config.retry) {
      error.config.retry = true
      try {
        const res = await axios.get(process.env.VUE_APP_API_URL+'refresh', { withCredentials: true })
        localStorage.setItem('token', res.data)
        return instance.request(error.config)
      }
      catch(e) { router.push('/auth') } 
      throw error
    }
    else store.commit('showMessage', { 
      color: '#ff4d4d', 
      icon: error.response?.data?.icon || 'hourglass_empty', 
      title: 'Помилка', 
      text: error.response?.data?.message || "Немає зв'язку із сервером. Спробуйте зайти пізніше"
    })
    return Promise.reject(error)

  }

)

export default instance